
* {
  margin: 0;
  padding: 0;
}
body {
  background: #59718a;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  overflow: hidden;
}

.login_wrap {
  width: 100%;
}
.login_wrap .login_box {
    width: 550px;
    margin: 0 auto;
    margin-top: 60px;
    background: rgba(255, 255, 255, 0.6);
    padding: 20px;
    border-radius: 6px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.login_wrap .login_box .title {
      text-align: center;
      font-size: 24px;
      margin: 15px auto;
}
.login_wrap .login_box .cutline {
      height: 30px;
      margin: 20px auto;
      font-size: 16px;
      color: #5e5e5e;
      line-height: 30px;
      text-align: center;
      background: url(/static/img/cutline.214d42f.png) center center no-repeat;
}
.login_wrap .login_box .line {
      width: 100%;
      height: 36px;
      margin-bottom: 10px;
}
.login_wrap .login_box .line:last-child {
        margin-top: 30px;
}
.login_wrap .login_box .line .label {
        width: 25%;
        display: inline-block;
        text-align: right;
        line-height: 36px;
        font-size: 14px;
        margin-right: 15px;
}
.login_wrap .login_box .line input {
        width: 70%;
        height: 36px;
        border: none;
        border-radius: 3px;
}
.login_wrap .login_box .line .btn {
        width: 100px;
        height: 36px;
        background: #59718a;
        text-align: center;
        line-height: 36px;
        font-size: 16px;
        margin: 0 auto;
        border-radius: 6px;
        color: white;
        cursor: pointer;
}

.home_wrap {
  width: 100%;
}
.home_wrap .navBar {
    width: 100%;
    height: 60px;
    background: white;
    -webkit-box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 12px;
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 12px;
}
